.left_panel {
  flex: 0.35;
  display: flex;
  flex-direction: column;
}

.lp_header {
  flex-basis: 65px;
  background-color: #f0f2f5;
  border-right: 1px solid rgba(165, 165, 165, 0.322);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 15px;
  padding-right: 15px;
}

.lp_profile_dp {
  height: 45px;
  width: 45px;
  border-radius: 25px;
  margin-top: 5px;
  margin-bottom: 5px;
  object-fit: cover;
}

.lp_profile_dp:hover {
  cursor: pointer;
}

.lp_header_icons {
  height: 50px;
  flex-basis: 200px;
  margin-left: 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.lp_icon {
  height: 25px;
  filter: invert(30%);
  transition: all 0.1s ease-in-out;
}

.lp_icon:hover {
  height: 30px;
  cursor: pointer;
}

.lp_input_field {
  flex-basis: 53px;
  background-color: rgb(255, 255, 255);
  position: relative;
  border-bottom: 1px solid rgba(165, 165, 165, 0.322);
}

.lp_srh_input {
  outline: 0;
  border: 0;
  background-color: rgb(240, 240, 240);
  border-radius: 5px;
  width: 80%;
  position: absolute;
  left: 15px;
  top: 8px;
  height: 35px;
  padding-left: 55px;
}

.lp_filter_icon {
  height: 15px;
  position: absolute;
  display: block;
  top: 18px;
  right: 7%;
  z-index: 2;
  filter: invert(30%);
  cursor: pointer;
}

.lp_search_icon {
  height: 15px;
  position: absolute;
  left: 35px;
  top: 18px;
  z-index: 2;
  filter: invert(20%);
}

.lp_hero_section {
  flex: 1;
  background-color: rgb(255, 255, 255);
  display: block;
  overflow-y: auto;
}

.lp_hero_section::-webkit-scrollbar {
  width: 7px;
}

.lp_hero_section::-webkit-scrollbar-thumb {
  background-color: rgb(192, 192, 192);
}

.lp_contact_list {
  height: 65px;
  display: flex;
  align-items: center;
  background-color: rgb(255, 255, 255);
}

.lp_contact_list:hover {
  background-color: rgb(243, 243, 243);
  cursor: pointer;
}

.lp_contact_dp {
  height: 50px;
  width: 50px;
  object-fit: cover;
  display: block;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 25px;
}

.lp_contact_details {
  width: 370px;
  height: 100%;
  border-bottom: 1px solid rgba(165, 165, 165, 0.322);
}

.lp_contact_details > h3,
.lp_no_cont_found > h3 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: normal;
  margin-top: 10px;
  font-size: 20px;
  margin-bottom: 0;
}

.lp_contact_details > p,
.lp_no_cont_found > p {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: normal;
  font-size: 15px;
  color: rgb(87, 87, 87);
  margin-top: 0px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.lp_no_cont_found {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  border-bottom: 1px solid rgba(165, 165, 165, 0.322);
}
