.right_panel {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.right_panel_header {
  flex-basis: 65px;
  background-color: #f0f2f5;
  display: flex;
  align-items: center;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
}

.rp_cont_dp {
  height: 45px;
  width: 45px;
  object-fit: cover;
  border-radius: 25px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.rp_cont_dp:hover {
  cursor: pointer;
}

.right_panel_header > h3 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-weight: normal;
  margin: 0;
  font-size: 20px;
  margin-left: 20px;
}

.rp_icon_place_holder {
  position: absolute;
  right: 0;
  width: 120px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
}

.rp_header_icon {
  height: 25px;
  filter: invert(30%);
  transition: all 0.1s ease-in-out;
}

.rp_header_icon:hover {
  height: 30px;
  cursor: pointer;
}

.right_panel_hero_section {
  flex: 1;
  padding: 40px;
  overflow-y: auto;
  scroll-behavior: smooth;
  background-image: url("https://images.alphacoders.com/132/1323469.png");
  opacity: 1;
  background-size: cover;
}

.right_panel_hero_section::-webkit-scrollbar {
  width: 7px;
}

.right_panel_hero_section::-webkit-scrollbar-thumb {
  background-color: rgb(192, 192, 192);
}

.rp_msg_design {
  display: block;
  position: relative;
  word-wrap: break-word;
  letter-spacing: 1px;
  min-width: 250px;
  margin-bottom: 5px;
  max-width: 60%;
  min-height: 25px;
  box-shadow: 0px 1px 2px rgba(21, 21, 21, 0.436);
  border-radius: 10px;
  padding: 10px;
  padding-bottom: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

.rp_msg_date {
  font-weight: 800;
  font-size: 11px;
  position: absolute;
  bottom: 3px;
  right: 5px;
}

.rp_msg_header {
  font-weight: 800;
  color: #000000;
  font-size: 12px;
  margin-bottom: 10px;
}

.rp_msg_you {
  background-color: #b4ffbc;
  float: right;
  clear: both;
}

.rp_msg_img {
  border-radius: 999px;
  position: absolute;
  right: 5px;
  top: 5px;
  height: 25px;
  width: 25px;
  object-fit: cover;
}

.rp_msg_cont {
  background-color: white;
  float: left;
  clear: both;
}

.right_panel_message_input {
  flex-basis: 65px;
  background-color: #f0f2f5;
  position: relative;
}

.rp_msg_icon {
  position: absolute;
  height: 25px;
  filter: invert(30%);
}

.rp_text_input {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  position: absolute;
  left: 9%;
  top: 10px;
  padding-left: 15px;
  font-size: 15px;
  width: 83%;
  height: 70%;
  outline: 0;
  border: 0;
  background-color: white;
  border-radius: 10px;
}

.rp_msg_icon:nth-child(1) {
  left: 3%;
  top: 20px;
}

.rp_msg_icon:nth-child(3) {
  top: 20px;
  right: 10%;
}

.rp_msg_icon:nth-child(4) {
  top: 20px;
  right: 2.5%;
}
