.login_cont {

}

.show_cont_details {
    background-color: rgba(249, 249, 249, 0.95);
    width: 100%;
    height: 100vh;
    position: fixed;
    display: block;
    flex-direction: column;
    z-index: 999;
}

@keyframes fade_out_animate {
    0% {opacity: 1;}
    100% {opacity: 0;}
}

.fade_out {
    animation: fade_out_animate 0.1s ease-in;
}

.show_cd_data_img {
    height: 50px;
    position: absolute;
    border-radius: 25px;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 40px;
}

.show_cd_data {
    background-color: white;
    flex-basis: 70px;
    display: flex;
    justify-content: space-between;
    position: relative;
    width: 100%;
    margin: 0;
    color: rgb(31, 31, 31);
}

.show_cd_data > h2 {
    margin-left: 85px;
    margin-right: 85px;
    margin-top: 23px;
    margin-bottom: 0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;
    font-weight: lighter;
}



.show_cd_img_container {
    flex: 1;
    height: 50%;
    display: flex;
    margin-top: 100px;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}

.member_id {
    width:  500px;
    height: 70px;
    font-size: 30px;
    text-align: center;
}

.login_but {
    width: 500px;
    background-color: greenyellow;
    height: 70px;
    font-size: 30px;
    cursor: pointer;
}
